import React from 'react';
import './Process.css';
import ProcessItem from "../ProcessItem/ProcessItem";

let process = [
    {
        title: <><br/> </>,
        text: <>   <br/>,  <br/>
             ,  ,<br/>  <br/>
                <br/>.</>
    },
    {
        title: <><br/></>,
        text: <> <br/>,  ,<br/>
              <br/>   <br/>.</>
    },
    {
        title: <> <br/></>,
        text: <>  <br/>  <br/>
              ,<br/>  <br/> <br/> .</>
    },
    {
        title: <><br/></>,
        text: <>  <br/> <br/>
             <br/>  <br/><br/>
               <br/>.</>
    },
]

export default function Process() {
    return <div className="process">
        {process.map((p, index) => <ProcessItem {...p} prefix={"/0"+(index+1)}/>)}
    </div>
}
